.btn-login {
  font-size: 0.9rem;
  letter-spacing: 0.05rem;
  padding: 0.75rem 1rem;
}

.btn-google {
  color: white !important;
  background-color: #ea4335;
}

.btn-facebook {
  color: white !important;
  background-color: #3b5998;
}
.input{
    bottom: 0;
    position: absolute;
    right: 0;
    display: flex;
}
.overflow{
    width: 100%;
    height: calc(100vh - 140px);
    overflow-y: auto;
    margin: 20px 0 72px 0;
    padding: 0 18px;
    display: flex;
    flex-direction: column;
    overflow-x: hidden;
}

